<template>
  <transition name="slide">
    <music-list :rank="true" :title="title" :bgImage="bgImage" :songs="songs"></music-list>    
  </transition>
</template>
<script>
  import MusicList from 'components/music-list/music-list'
  import {getMusicList} from 'api/rank'
  import {ERR_OK} from 'api/config'
  import {mapGetters} from 'vuex'
  import {createSong} from 'common/js/song'
  export default{

    computed:{
      ...mapGetters([
        'rankList',
      ]),
      title(){
        return this.rankList.topTitle
      },
      bgImage(){
        if(this.songs.length){
          return this.songs[0].image
        }
      }
    },
    data(){
      return {
        songs:[]
      }
    },
    created(){
      this._getRankList()
    },
    methods:{
      _getRankList(){
         if (!this.rankList.id) {
          this.$router.push('/rank')
          return
        }
        getMusicList(this.rankList.id).then((res)=>{
          if(res.code === ERR_OK){
            this.songs = this._createSongList(res.songlist)
            console.log(this.songs[0].image,1)
          }
        })
        
      },
     _createSongList(list){
        let ret =[]
        list.forEach((item)=>{
          const itemData = item.data
          if (itemData.songid && itemData.albummid) {
            ret.push(createSong(itemData))
          }
        })
        return ret
     }
    },
    components:{
      MusicList
    }
  }
</script>
<style>

</style>




