<template>
  <div class='music-list'>
    <div class="back">
      <i class="icon-back" @click="back"></i>
    </div>
    <h1 class="title" v-html="title"></h1>
    <div class="bg-image" :style='bgstyle' ref="bgImage">
      <div class="play-wrapper" @click="random" v-show="songs.length>0" ref="playButton"> 
        <div class="play">
          <i class="icon-play"></i>
          <span class="text">随机播放全部</span>
        </div>
      </div>
      <div class="filter"></div>
    </div>
    <div class="bg-layer" ref="layer"></div>
    <scroll @scroll='scroll'
            :probe-type='probeType' 
            :data='songs' 
            class="list" 
            ref="list"
            :listen-scroll='listenScroll'
            >
      <song-list @select="selectItem" :songs='songs' :rank="rank"></song-list>
      <div class="loading-container" v-show="!songs.length">
        <loading></loading>
      </div>
    </scroll>
  </div>
</template>

<script>//歌手 页面
import scroll from 'base/scroll/scroll'//滑动组件
import SongList from 'base/songlist/songlist'//歌手的歌曲列表的组件
import loading from 'base/loading/loading'
import {mapActions,mapGetters} from 'vuex'
import {playlistMixin} from 'common/js/mixin'
export default {
  mixins:[playlistMixin],//处理当小播放器出现的时候底部有一部分被遮住的问题
  props:{
    bgImage:{
      type:String,
      default:''
    },
    songs:{
      type:Array,
      default:[]
    },
    title:{
      type:String,
      default:''
    },
    rank:{
      type:Boolean,
      default:false
    }
  },
  data(){
    return{
      scrollY:-1,
      RESERVED_HEIGHT:40
    }
  },
  computed:{
    bgstyle(){
      return 'background-image:url('+this.bgImage+')'
    }
  },
  mounted(){
    this.imgHeight = this.$refs.bgImage.clientHeight
    this.minTranslateY = -this.imgHeight
    this.$refs.list.$el.style.top = `${this.$refs.bgImage.clientHeight}px`
  },
  components:{
    scroll,
    SongList,
    loading
  },
  created(){
    this.probeType = 3
    this.listenScroll = true
  },
  methods:{
    handlePlaylist(playlist){
      console.log(this.$refs.list.$el.style)
      const bottom = playlist.length > 0 ? '60px':''
      this.$refs.list.$el.style.bottom = bottom 
      this.$refs.list.refresh()
    },
    random(){
      this.randomPlay({
        list:this.songs
      })
    },
    scroll(pos){
      this.scrollY=pos.y
    },
    back(){
      this.$router.back()
    },
    selectItem(item,index){
      this.selectPlay({
        list:this.songs,
        index:index
      })
    },
    ...mapActions([
      'selectPlay',
      'randomPlay'
    ]),
    ...mapGetters([
      'mode'
    ])
  },
  watch:{
    scrollY(newy){//监测scrolly的滑动
      let translateY = Math.max(this.minTranslateY+this.RESERVED_HEIGHT,newy)
      let zIndex = 10
      let scale = 1
      this.$refs.layer.style['transform'] = `translate3d(0,${translateY}px,0) `
      this.$refs.layer.style['webkittransform'] = `translate3d(0,${translateY}px,0) `
      const percent = Math.abs(newy / this.imgHeight)
      if(newy > 0){
        scale = 1 + percent
      }
      if(newy<translateY){
        this.$refs.playButton.style.display = 'none'
        this.$refs.bgImage.style.paddingTop = 0
        this.$refs.bgImage.style.height = `${this.RESERVED_HEIGHT}px`
      }else{
        this.$refs.bgImage.style.paddingTop = '70%'
        this.$refs.bgImage.style.height = 0
        this.$refs.playButton.style.display = ''
         zIndex = 0
      }
      this.$refs.bgImage.style.zIndex = zIndex
      this.$refs.bgImage.style['transform'] = `scale(${scale})`
      this.$refs.bgImage.style['webkitTransform'] = `scale(${scale})`

  }
  }
}
</script>
<style scoped lang="stylus" rel="stylesheet/stylus">
 @import "~common/stylus/variable" ;
  .music-list
    position: fixed
    z-index: 100
    top: 0
    left: 0
    bottom: 0
    right: 0
    background: $color-background
    .back
      position absolute
      top: 0
      left: 6px
      z-index: 50
      .icon-back
        display: block
        padding: 10px
        font-size: $font-size-large-x
        color: $color-theme
    .title
      position: absolute
      top: 0
      left: 10%
      z-index: 40
      width: 80%
      no-wrap()
      text-align: center
      line-height: 40px
      font-size: $font-size-large
      color: $color-text
    .bg-image
      position: relative
      width: 100%
      height: 0
      padding-top: 70%
      transform-origin: top
      background-size: cover
      .play-wrapper
        position: absolute
        bottom: 20px
        z-index: 50
        width: 100%
        .play
          box-sizing: border-box
          width: 135px
          padding: 7px 0
          margin: 0 auto
          text-align: center
          border: 1px solid $color-theme
          color: $color-theme
          border-radius: 100px
          font-size: 0
          .icon-play
            display: inline-block
            vertical-align: middle
            margin-right: 6px
            font-size: $font-size-medium-x
          .text
            display: inline-block
            vertical-align: middle
            font-size: $font-size-small
      .filter
        position: absolute
        top: 0
        left: 0
        width: 100%
        height: 100%
        background: rgba(7, 17, 27, 0.4)
    .bg-layer
      position: relative
      height: 100%
      background: $color-background
    .list
      position: fixed
      top: 0
      bottom: 0
      width: 100%    
      background: $color-background
      .song-list-wrapper
        padding: 20px 30px
      .loading-container
        position: absolute
        width: 100%
        top: 50%
        transform: translateY(-50%)
</style>



